@import url("https://fonts.googleapis.com/css2?family=Inter:wght@500;700;800&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

br::selection {
  background: none;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
*::selection {
  background-color: #c4132f;
  color: #fff;
}

body {
  background-color: #e5eaee;
  overflow: hidden;
}
body.no-scroll {
  padding-right: 17px;
  overflow: hidden !important;
}
body.loaded {
  display: block;
  overflow: auto;
}

.wrap {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 24px;
}

body {
  font-family: "Inter", sans-serif;
}

h1 {
  font-size: calc(50px + 30 * (100vw - 300px) / 1300);
  font-weight: 800;
}

h2 {
  font-size: calc(40px + 20 * (100vw - 300px) / 1300);
  font-weight: 800;
}

h3 {
  font-size: calc(18px + 6 * (100vw - 300px) / 1300);
  font-weight: 700;
}

h4 {
  font-size: 1rem;
  font-size: calc(15px + 1 * (100vw - 300px) / 1300);
  font-weight: 700;
  letter-spacing: 0.08em;
}

p {
  font-size: calc(15px + 1 * (100vw - 300px) / 1300);
  font-weight: 500;
  line-height: 1.8;
}

h1,
h2,
h3,
h4 {
  text-transform: uppercase;
  line-height: 1.2;
}

.light {
  color: #fff;
}

.dark {
  color: #181818;
}

.accent {
  color: #c4132f;
}

.nav {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  transition: all 0.3s cubic-bezier(0, 0, 0.2, 1);
}
@media (max-width: 43.75em) {
  .nav__logo #logo-text {
    display: none;
  }
}
.nav.fill {
  background-color: #181818;
}
.nav.scroll-down {
  transition: all 1s cubic-bezier(0, 1.5, 1, 1.5);
  transform: translate3d(0, -110%, 0);
}
.nav::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.5);
  z-index: -1;
}
.nav__wrap {
  padding-right: 0;
  height: 95px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 61.25em) {
  .nav__wrap {
    padding-right: 24px;
  }
}
.nav__links {
  color: #fff;
  height: 100%;
  display: flex;
  align-items: center;
  gap: 32px;
}
@media (max-width: 90em) {
  .nav__links {
    gap: 8px;
  }
}
@media (max-width: 61.25em) {
  .nav__links {
    display: none;
  }
}
.nav__link {
  opacity: 0.5;
  text-transform: uppercase;
  padding: 10px;
  letter-spacing: 0.08em;
  cursor: pointer;
}
.nav__link.active {
  opacity: 1;
}
.nav__link:not(.active):not(.cta):hover {
  opacity: 0.75;
}
.nav__link.cta {
  height: 100%;
  display: flex;
  align-items: center;
  gap: 26px;
  justify-content: center;
  opacity: 1;
  padding: 10px 76px;
  background-color: #c4132f;
}
.nav__menu-toggle {
  z-index: 100000;
  display: block;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
}
@media (min-width: 61.25em) {
  .nav__menu-toggle {
    display: none;
  }
}
.nav__menu-toggle.active {
  margin-right: 17px;
}
.nav__menu-toggle.active span {
  background: #181818;
  transform: rotate(45deg) translate(-2px, -5px);
}
.nav__menu-toggle.active span:nth-last-child(2) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}
.nav__menu-toggle.active span:nth-last-child(1) {
  transform: rotate(-45deg) translate(0, -1px);
}
.nav__menu-toggle span {
  display: block;
  background: #fff;
  position: relative;
  width: 33px;
  height: 4px;
  border-radius: 3px;
  z-index: 1;
  transform-origin: 4px 0px;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
}
.nav__menu-toggle span:not(:last-child) {
  margin-bottom: 5px;
}
.nav__menu-toggle span:first-child {
  transform-origin: 0% 0%;
}
.nav__menu-toggle span:last-child {
  transform-origin: 0% 0%;
}
.nav__menu-toggle span:nth-last-child(2) {
  transform-origin: 0% 100%;
}
.nav__backdrop {
  z-index: 99;
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100%;
  background: rgba(24, 24, 24, 0.6);
  pointer-events: none;
  opacity: 0;
  transition: all 0.3s ease;
}
.nav__mobile {
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
  min-height: 120vh;
  width: 100%;
  max-width: 400px;
  z-index: 100;
  background-color: #fff;
  padding: 119px 24px calc(20vh + 24px);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}
@media (min-width: 61.25em) {
  .nav__mobile {
    display: none;
  }
}
.nav__mobile h3 {
  margin-bottom: 16px;
}
.nav__mobile h4 {
  margin-bottom: 24px;
}
.nav.mobile-active .nav__backdrop {
  opacity: 1;
  pointer-events: all;
  cursor: pointer;
}
.nav.mobile-active .nav__mobile {
  transform: translateX(0);
}

.team__cta {
  width: fit-content;
  margin: 80px auto 0;
  padding: 24px 64px;
  text-transform: uppercase;
  appearance: none;
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
  transition: all 0.3s ease;
  outline: none;
  border: none;
}
.team__cta:hover {
  transform: scale(0.98);
}
.team__cta:disabled {
  opacity: 0;
  pointer-events: none;
}

.backdrop {
  pointer-events: none;
  opacity: 0;
  z-index: 10000;
  position: fixed;
  inset: 0;
  width: 100%;
  min-height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  padding: 95px 0;
  cursor: pointer;
  overflow-y: scroll;
  transition: opacity 0s;
}
.backdrop.active {
  transition: opacity 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 1;
  pointer-events: all;
}
.backdrop .news-dialog {
  overflow: hidden;
  cursor: default;
  position: relative;
  width: 100%;
  max-width: 1400px;
  border: none;
  padding: 80px 100px;
}
@media (max-width: 43.75em) {
  .backdrop .news-dialog {
    padding: 80px 24px;
  }
}
.backdrop .news-dialog__close {
  position: absolute;
  top: 24px;
  right: 24px;
  font-size: 35px;
  line-height: 1;
  cursor: pointer;
}
.backdrop .news-dialog__top {
  max-width: 600px;
  margin-bottom: 80px;
}
.backdrop .news-dialog__content {
  width: 100%;
  display: grid;
  grid-template-columns: 300px 1fr;
}
@media (max-width: 56.25em) {
  .backdrop .news-dialog__content {
    grid-template-columns: 100px 1fr;
  }
}
@media (max-width: 43.75em) {
  .backdrop .news-dialog__content {
    grid-template-columns: 1fr;
  }
}
.backdrop .news-dialog__content .social {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 42px;
  width: 100px;
  z-index: 2;
}
@media (max-width: 43.75em) {
  .backdrop .news-dialog__content .social {
    padding: 80px 0;
    flex-direction: row;
  }
}
.backdrop .news-dialog__content .text {
  display: flex;
  flex-direction: column;
  max-width: 630px;
}

.loader {
  z-index: 1000;
  position: fixed;
  display: block;
  inset: 0;
  width: 100%;
  height: 100%;
  background: #e5eaee;
  transition: opacity 0.3s cubic-bezier(0.4, 0, 0.6, 1);
  pointer-events: none;
}
.loader.leaving {
  opacity: 0;
}

.custom-slider {
  width: 100%;
  height: 400px;
  position: relative;
}
.custom-slider *::selection {
  background: none;
}
@media (max-width: 43.75em) {
  .custom-slider {
    width: calc(100% + 48px);
    margin-left: -24px;
  }
}
.custom-slider .next,
.custom-slider .prev {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}
.custom-slider .next {
  right: -40px;
  transform: translateY(-50%) rotate(180deg);
}
@media (max-width: 43.75em) {
  .custom-slider .next {
    right: 24px;
    bottom: 24px;
    z-index: 10;
  }
}
.custom-slider .prev {
  left: -40px;
}
@media (max-width: 43.75em) {
  .custom-slider .prev {
    left: unset;
    left: 24px;
    bottom: 24px;
    z-index: 10;
  }
}
.custom-slider__wrap {
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  display: flex;
  gap: 64px;
  height: 100%;
  background: red;
}
.custom-slider__slide {
  height: 100%;
  min-width: 100%;
}
.custom-slider__slide img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.landing-page {
  z-index: 1;
  position: relative;
  max-height: 960px;
  height: 100vh;
  clip-path: polygon(100% 0, 100% 90%, 50% 100%, 0 90%, 0 0);
  display: flex;
  align-items: center;
}
@media (max-width: 56.25em) {
  .landing-page {
    clip-path: polygon(100% 0, 100% 100%, 100% 100%, 0 80%, 0 0);
  }
}
.landing-page::after {
  position: absolute;
  inset: 0;
  content: "";
  width: 100%;
  height: 100%;
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.66) 6.22%, rgba(255, 255, 255, 0) 51.23%);
}
.landing-page__background {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  max-height: 1200px;
  object-fit: cover;
  z-index: -2;
}
.landing-page__wrap {
  z-index: 1;
}
.landing-page__wrap p {
  max-width: 420px;
}
.landing-page__wrap h1 {
  max-width: 640px;
  margin-bottom: 24px;
}

.news {
  width: 100%;
  transform: translateY(-200px);
  margin-bottom: -200px;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr 2fr;
  max-width: 3840px;
}
@media (max-width: 56.25em) {
  .news {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 43.75em) {
  .news {
    grid-template-columns: 1fr;
  }
}
.news__container {
  cursor: pointer;
  position: relative;
  padding: 265px 27px 35px;
  background: transparent;
  display: flex;
  flex-direction: column;
  transition: all 0.2s ease;
}
@media (max-width: 43.75em) {
  .news__container:not(:first-of-type) {
    padding: 65px 27px 35px;
  }
}
.news__container:hover {
  transform: translateY(20px);
}
@media (max-width: 56.25em) {
  .news__container:hover {
    transform: scale(0.99);
  }
}
.news__container:nth-of-type(even) {
  background-color: #181818;
}
@media (max-width: 56.25em) {
  .news__container:nth-of-type(3) {
    padding: 65px 27px 35px;
    grid-column: 1/3;
  }
}
@media (max-width: 43.75em) {
  .news__container:nth-of-type(3) {
    grid-column: unset;
  }
}
.news__container:nth-of-type(3) p {
  max-width: 420px;
}
.news__container__mask {
  position: absolute;
  object-fit: cover;
  inset: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.news__container__date {
  margin-bottom: 24px;
}
.news__container__headline, .news__container__subtitle {
  margin-bottom: 8px;
}
.news__container p {
  max-width: 300px;
}

.grid-section {
  background-color: #fff;
  padding: 180px 0;
  width: 100%;
  margin: 0 auto;
}
.grid-section .wrap {
  width: 100%;
  max-width: 1400px;
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 64px;
  padding-bottom: 200px;
}
@media (max-width: 56.25em) {
  .grid-section .wrap {
    grid-template-columns: 1fr;
    padding-bottom: 0px;
  }
}
.grid-section__container {
  z-index: 1;
}
.grid-section__custom-asset {
  height: 400px;
  object-fit: cover;
  z-index: 0;
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translateY(calc(50% + 200px));
}
@media (max-width: 56.25em) {
  .grid-section__custom-asset {
    position: relative;
    transform: none;
    max-width: unset;
    width: calc(100% + 48px);
    margin-left: -24px;
  }
}

.team {
  width: 100%;
  padding: 380px 0 180px;
  background: #181818;
  display: flex;
  flex-direction: column;
}
@media (max-width: 56.25em) {
  .team {
    padding: 180px 0 180px;
  }
}
.team .top {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 130px;
}
@media (max-width: 56.25em) {
  .team .top {
    flex-direction: column;
    align-items: flex-start;
  }
}
.team__wrap {
  position: relative;
  overflow: hidden;
  height: 0;
  padding: 0;
  transition: height 0.3s ease-out;
}
.team__wrap__cards {
  position: absolute;
  inset: 0;
  width: 100%;
  height: fit-content;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
.team__wrap__card {
  position: relative;
  z-index: 0;
  transition: opacity 0.3s cubic-bezier(0.36, 0.61, 0.83, 0.67);
}
.team__wrap__card.loading {
  opacity: 0;
}
.team__wrap__card.loaded {
  opacity: 1;
}
@media (min-width: 32.5em) {
  .team__wrap__card.open-right .team__wrap__card__details {
    opacity: 1;
    transition: transform 0.3s ease;
    transform: translateX(100%);
  }
}
@media (min-width: 32.5em) {
  .team__wrap__card.open-left .team__wrap__card__details {
    opacity: 1;
    transition: transform 0.3s ease;
    transform: translateX(-100%);
  }
}
@media (max-width: 32.5em) {
  .team__wrap__card.open-left img {
    opacity: 0;
    transition: opacity 0.4s ease;
  }
  .team__wrap__card.open-left .team__wrap__card__details {
    opacity: 1;
  }
}
.team__wrap__card:hover {
  z-index: 1;
}
.team__wrap__card:hover img {
  filter: grayscale(0%);
}
.team__wrap__card img {
  aspect-ratio: 1;
  object-fit: cover;
  width: 100%;
  height: 100%;
  z-index: 2;
  filter: grayscale(100%);
  transition: all 0.4s ease;
}
.team__wrap__card__details {
  pointer-events: none;
  padding: 24px;
  z-index: -1;
  background-color: #fff;
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  transition: transform 0s ease;
  opacity: 0;
}
.team__wrap__card__details h3 {
  margin-bottom: 16px;
}
.team__filters {
  margin-top: 24px;
  padding: 0;
  display: flex;
  gap: 16px;
}
@media (max-width: 43.75em) {
  .team__filters {
    flex-direction: column;
  }
}
.team__filters__filter {
  padding: 10px;
  text-transform: uppercase;
  cursor: pointer;
  transition: color 0.2s ease;
}
.team__filters__filter:not(.active):hover {
  color: #fff;
  opacity: 0.5;
}
.team__filters__filter.active {
  color: #c4132f;
}

.footer {
  width: 100%;
  display: flex;
  align-items: stretch;
}
@media (max-width: 56.25em) {
  .footer {
    flex-direction: column-reverse;
  }
}
.footer__split {
  flex: 1;
  display: flex;
  flex-direction: row-reverse;
}
@media (max-width: 56.25em) {
  .footer__split {
    flex-direction: row;
  }
}
@media (min-width: 56.25em) {
  .footer__split:last-of-type .footer__split__wrap {
    padding: 80px 24px 80px 100px;
  }
}
@media (max-width: 56.25em) {
  .footer__split:last-of-type .footer__split__wrap {
    padding: 80px 24px;
  }
}
@media (max-width: 43.75em) {
  .footer__split:last-of-type .footer__split__wrap .footer__split__wrap__social {
    align-self: center;
  }
}
.footer__split__wrap {
  padding: 80px 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  max-width: 700px;
}
.footer__split__wrap .copy {
  margin-top: 24px;
}
.footer__split__wrap ul {
  padding: 0;
}
.footer__split__wrap__social {
  display: flex;
  width: 100%;
  max-width: 300px;
  align-items: center;
  justify-content: space-between;
}
.footer__split__wrap__social li {
  cursor: pointer;
}
.footer__split__wrap__links {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 80px;
  margin-top: 0;
}
.footer__split__wrap__links li {
  flex: 50%;
  box-sizing: border-box;
}
.footer__split__wrap__links li span {
  display: block;
  width: fit-content;
  cursor: pointer;
  padding: 10px;
}
.footer__split__logo {
  max-width: 416px;
}
.footer__split:first-of-type {
  background: #181818;
}
.footer__split:last-of-type {
  background: #c4132f;
  flex-direction: row;
}